import React from 'react';

class SetTitle extends React.Component {
    componentDidMount() {
        this.getBotName();
    }

    getBotName = () => {
        fetch('/api/general/name', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => document.title = `${json_data.data.name} Dashboard`)
        .catch(e => {
            console.trace(e);
            document.title = `JJMumbleBot Dashboard`;
        });
    };

    render() {
        return(<div/>);
    }
}

export default SetTitle;