import React from 'react';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Grid, Typography} from '@material-ui/core';
import styled from 'styled-components';
import Parser from 'html-react-parser';


const ThemedTable = styled(Table)`
    .tableHead {
        background: ${({ theme }) => theme.contrast2};
    }
    .outTableBody {
        height: 100px;
        overflow: hide;
    }
`;
const ThemedTableCell = styled(TableCell)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};   
`;

class LatestCommand extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            last_cmd_type: "",
            last_cmd: ""
        };
    }

    componentDidMount() {
        if (Object.keys(this.props.socketData).length !== 0) {
            this.setState({
                last_cmd: this.props.socketData.last_cmd_output,
                last_cmd_type: this.props.socketData.last_cmd_type,
            })
        }
    }

    componentDidUpdate(prev_props, prev_state) {
        if (prev_props.socketData.last_cmd_output !== this.props.socketData.last_cmd_output) {
            this.setState({
                last_cmd: this.props.socketData.last_cmd_output,
                last_cmd_type: this.props.socketData.last_cmd_type,
            });
        }
    }

    render() {
        return (
            <Grid item xs>
                <TableContainer component="span">
                    <Paper elevation={3}>
                    <ThemedTable className="cmdTable">
                        <TableHead>
                            <TableRow>
                                <ThemedTableCell className="tableHead">
                                    <Typography variant="body2"><b>Latest Command Output {this.state.last_cmd_type}</b></Typography>
                                </ThemedTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody className="tableBody">
                            <TableRow>
                                <ThemedTableCell className="recentCmdCell">
                                    {Parser(this.state.last_cmd)}
                                </ThemedTableCell>
                            </TableRow>
                        </TableBody>
                    </ThemedTable>
                    </Paper>
                </TableContainer>
            </Grid>
        )
    }
}

export default LatestCommand;