import React, {useEffect, useState, useRef} from "react";
import { ThemeProvider } from "styled-components";
import { GlobalStyles } from "./components/theme_selectors/GlobalStyles";
import { lightTheme, darkTheme } from "./components/theme_selectors/Themes";
import { UseDarkMode } from "./components/theme_selectors/UseDarkMode";
import SetTitle from "./components/set_title";
import Toggle from "./components/theme_selectors/ThemeToggler";
import CommandFormGrid from './components/CommandInputGrid';
import CommandHistory from './components/CommandHistory';
import LatestCommand from './components/LatestCommand';
import OnlineUsers from './components/OnlineUsers';
import ReactJson from 'react-json-view';
import PluginsList from './components/plugins_list';
import TopNavBar from "./components/nav_bar";
import styled from 'styled-components';
import {Snackbar, IconButton} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import CachedIcon from '@material-ui/icons/Cached';
import './App.css';
import url from 'url';

//let socket = new WebSocket("ws://192.168.1.200:7000/ws");

// localStorage.clear();

const ThemedJSONViewer = styled(ReactJson)`
  background-color: ${({ theme }) => theme.body};
  color: ${({ theme }) => theme.text};
`;
const ThemedErrorAlert = styled(Alert)`
  background-color: ${({ theme }) => theme.black};
  color: ${({ theme }) => theme.white};
`;
const ThemedIconAlert = styled(IconButton)`
  color: ${({ theme }) => theme.white};
`;

function App() {
  const webSocket = useRef(null);
  const [connectionError, setConnectionError] = useState(false);
  const [connectionErrorMsg, setConnectionErrorMsg] = useState("");
  const [socketData, setSocketData] = useState({});

  const [theme, themeToggler] = UseDarkMode();
  const themeMode = theme === 'light' ? lightTheme : darkTheme;
  const themeText = theme === 'light' ? 'Light Mode' : 'Dark Mode';
  const themeToggle = <Toggle theme={theme} themeText={themeText} toggleTheme={themeToggler} />;

  const getSocketData = () => {
    webSocket.current.onmessage = (msg) => {
      setSocketData(JSON.parse(msg.data));
    };
  };

  useEffect(() => {
    webSocket.current = new WebSocket(
      url.format({
        protocol: window.location.protocol === 'https:' ? 'wss' : 'ws',
        hostname: window.location.hostname,
        port: window.location.port || 7000,
        pathname: '/ws',
        slashes: true,
      })
    );
    webSocket.current.onerror = () => {
      setConnectionError(true);
      setConnectionErrorMsg(`There was an error connecting to the web socket endpoint!`);
    };
  }, []);

  useEffect(() => {
    getSocketData();
  }, [socketData]);

  const reloadPage = () => {
    window.location.reload(false);
  }

  return (
    <ThemeProvider theme={themeMode}>
      <SetTitle />
      <GlobalStyles />
      <Snackbar 
        anchorOrigin={{vertical: 'top', horizontal: 'center'}}
        open={connectionError}>
          <ThemedErrorAlert 
            severity="error"
            action={
              <ThemedIconAlert component="span" size="small" onClick={reloadPage}>
                <CachedIcon/>
              </ThemedIconAlert>
            }>
            {connectionErrorMsg}
          </ThemedErrorAlert>
      </Snackbar>

      <TopNavBar
        themeToggle={themeToggle}
      tabs={
        [
          [
            <CommandFormGrid key="main-cmd-form-key"/>, 
            <LatestCommand socketData={socketData} key="latest-cmd-key"/>, 
            <CommandHistory socketData={socketData} key="cmd-history-key"/>, 
            <OnlineUsers socketData={socketData} key="online-users-key"/>
          ],
          [<PluginsList key="plugins-list-key"/>],
          [<ThemedJSONViewer key="json-viewer"
            src={socketData} 
            iconStyle="square" 
            collapsed={2} 
            displayDataTypes={false} 
            theme={theme === 'light' ? {
                base00: 'rgba(0, 0, 0, 0)',
                base01: 'rgb(245, 245, 245)',
                base02: '#2e2e2e',
                base03: '#93a1a1',
                base04: 'rgba(0, 0, 0, 0.3)',
                base05: '#586e75',
                base06: '#073642',
                base07: '#002b36',
                base08: '#d33682',
                base09: '#cb4b16',
                base0A: '#dc322f',
                base0B: '#859900',
                base0C: '#6c71c4',
                base0D: '#586e75',
                base0E: '#2aa198',
                base0F: '#268bd2'
              } : {
                base00: "rgba (0, 0, 0, 0)",
                base01: "#202020",
                base02: "#c2c2c2",
                base03: "#505050",
                base04: "#b0b0b0",
                base05: "#d0d0d0",
                base06: "#e0e0e0",
                base07: "#ffffff",
                base08: "#eb008a",
                base09: "#f29333",
                base0A: "#f8ca12",
                base0B: "#37b349",
                base0C: "#00aabb",
                base0D: "#0e5a94",
                base0E: "#b31e8d",
                base0F: "#7a2d00"
              }
            } />],
        ]
        }
      />
    </ThemeProvider>
  );
}

export default App;
