import React from 'react';
import styled from "styled-components"
import IconButton from '@material-ui/core/IconButton';
import LoopIcon from '@material-ui/icons/Loop';
import {Alert} from '@material-ui/lab';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Collapse, Grid} from '@material-ui/core';


const RefreshButton = styled(IconButton)`
    background-color: rgb(0,0,0,0);
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;

const ThemedAlert = styled(Alert)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;

const ThemedTable = styled(Table)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
    th {
        background: ${({ theme }) => theme.contrast2};
    }
`;
const ThemedTableCell = styled(TableCell)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
`;

class PluginsList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAlert: false,
            alertMsg: "",
            alertSeverity: "",
            items: []
        }
    }

    refreshPlugins = (url, useAlert) => {
        fetch(url, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({items: json_data.data.plugins});
            if (useAlert) {
                this.setState({showAlert: true, alertMsg: "Successfully refreshed the plugins list!", alertSeverity: "success"});
            }
        }).catch(e => {
            console.log(e);
            this.setState({showAlert: true, alertMsg: "Error retrieving the plugins list!", alertSeverity: "error"});
        });
    };

    closeAlert = () => {
        this.setState({showAlert: false, alertMsg: "", alertSeverity: ""});
    }

    componentDidMount() {
        this.refreshPlugins('/api/plugins', false);
    }

    render() {
        let alert = this.state.showAlert ? <ThemedAlert onClose={this.closeAlert} severity={this.state.alertSeverity}>{this.state.alertMsg}</ThemedAlert> : "";
        return (
            <Grid item xs={12} sm={4}>
                <Collapse in={this.state.showAlert}>
                    {alert}
                </Collapse>
                <TableContainer component={Paper}>
                    <ThemedTable>
                        <TableHead>
                            <TableRow>
                                <ThemedTableCell component="th">
                                    <b>Active Plugins:</b>  
                                    <RefreshButton 
                                    id="refreshButton" 
                                    onClick={() => this.refreshPlugins('/api/plugins', true)}
                                    size="small"
                                    >
                                        <LoopIcon />
                                    </RefreshButton>
                                </ThemedTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                this.state.items.map(item => 
                                (
                                    <TableRow key={item}>
                                        <ThemedTableCell component="td" scope="row">
                                        {item}
                                        </ThemedTableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </ThemedTable>
                </TableContainer>
            </Grid>
        );
    }
    
}

export default PluginsList;