import React from 'react';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Grid, Typography, Paper} from '@material-ui/core';
import AccountTreeRoundedIcon from '@material-ui/icons/AccountTreeRounded';
import TreeView from '@material-ui/lab/TreeView';
import styled from 'styled-components';
import NestedList from './NestedList.js';
import { makeStyles } from '@material-ui/core/styles';


const ThemedTable = styled(Table)`
    background: ${({ theme }) => theme.contrast};
    padding: 5em;
    .tableHead {
        background: ${({ theme }) => theme.contrast2};
    }
`;
const ThemedTableCell = styled(TableCell)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};   
`;


class OnlineUsers extends React.Component {
    constructor(props) {
        super(props);
        this.max_expand = Array.from({length: 256}, (_, i) => i+1+"")
        this.state = {
            botName: "",
            list_hash: "",
            list_items: []
        };
        this.classes = this.useStyles()
    }

    useStyles = () => makeStyles({
        root: {
          height: 500,
          flexGrow: 1,
          maxWidth: 400,
        },
      });

    refreshBotName = () => {
        fetch('/api/general/name', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({botName: json_data.data.name});
        }).catch(e => {
            console.log(e);
            this.setState({botName: 'N/A'});
        });
    };

    componentDidMount() {
        if (Object.keys(this.props.socketData).length !== 0) {
            this.setState({
                list_items: this.props.socketData.server_hierarchy.hierarchy,
                list_hash: this.props.socketData.server_hierarchy.hash
            })
        }
        this.refreshBotName();
    }

    componentDidUpdate(prev_props, prev_state) {
        if (Object.keys(this.props.socketData).length > 0) {
            if (prev_props.socketData.server_hierarchy) {
                if (this.state.list_hash !== this.props.socketData.server_hierarchy.hash) {
                    this.setState({
                        list_items: this.props.socketData.server_hierarchy.hierarchy,
                        list_hash: this.props.socketData.server_hierarchy.hash
                    });
                }
            }
        }
    }

    render() {
        return (
            <Grid item xs>
                <TableContainer component="span">
                    <Paper elevation={3}>
                    <ThemedTable className="cmdTable">
                        <TableHead>
                            <TableRow>
                                <ThemedTableCell className="tableHead">
                                    <Typography variant="body2"><b>Channels</b> <AccountTreeRoundedIcon/></Typography>
                                </ThemedTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody className="tableBody">
                            <TreeView
                                defaultExpanded={this.max_expand}>
                                    <NestedList
                                    key="online-users-hierarchy-key"
                                    bot_name={this.state.botName}
                                    items={this.state.list_items} 
                                    item_label={<Typography variant="body2" gutterBottom={true} noWrap>All Channels</Typography>}
                                    node_id={1}/>
                            </TreeView>
                        </TableBody>
                    </ThemedTable>
                    </Paper>
                </TableContainer>
            </Grid>
        )
    }
}

export default OnlineUsers;