import React from 'react';
import Button from '@material-ui/core/Button';
import KeyboardReturnIcon from '@material-ui/icons/KeyboardReturn'; 
import {TextField, IconButton, Grid, Paper} from '@material-ui/core';
import styled from "styled-components"


const ThemedTextField = styled(TextField)`
    & label {
        color: ${({ theme }) => theme.text};
    }
    & input {
        color: ${({ theme }) => theme.text};
        width: 100%;
    }
`;
const ThemedSubmitButton = styled(Button)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;
const ThemedIconButton = styled(IconButton)`
    color: ${({ theme }) => theme.text};
    &:hover {
        color: ${({ theme }) => theme.text};
    }
`;

const ThemedPaper = styled(Paper)`
    display: 'block';
    padding: 0.2em;
    background-color: ${({ theme }) => theme.body};
`;


class CommandFormGrid extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            botName: '',
            botCommandToken: '',
            commandText: '',
            cmdInvalid: true,
            invalidCmdText: '',
            sentCmd: ''
        }
        this.updateCommandText = this.updateCommandText.bind(this);
        this.sendCommand = this.sendCommand.bind(this);
    }

    refreshBotDetails = () => {
        fetch('/api/general', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({botName: json_data.data.name});
            this.setState({botCommandToken: json_data.data.command_token});
        }).catch(e => {
            console.log(e);
            this.setState({botName: 'N/A'});
            this.setState({botCommandToken: '!'});
        });
    };

    updateCommandText = (evt) => {
        this.setState({commandText: evt.target.value})
        if (this.cmdIsValid()) {
            this.setState({cmdIsValid: true});
        }
        else {
            this.setState({cmdIsValid: false});
        }
    }

    sendCommand = (evt) => {
        evt.preventDefault();
        if (this.cmdIsValid()) {
            this.setState({sentCmd: this.state.commandText});

            fetch(`/api/command`, {
                method: 'POST',
                body: JSON.stringify({"text": this.state.commandText})
            })
            .then(res => (res.ok ? res : Promise.reject(res)))
            .then(res => res.json())
            .then(json_data => {
                console.log(json_data.status);
            })
            .catch(e => console.log(e));
            this.setState({commandText: ''});
        }
        else {
            this.setState({commandText: ''});
        }
        this.setState({cmdIsValid: false});
    }

    cmdIsValid = () => {
        if (this.state.commandText.length > 0 && this.state.commandText.charAt(0) === this.state.botCommandToken) {
            return true;
        }
        return false;
    }

    componentDidMount() {
        this.refreshBotDetails(this.props.url);
    }

    render() {
        return (
            <Grid item xs={12}>
                <ThemedPaper elevation={3}>
                <form onSubmit={this.sendCommand} autoComplete="off">
                    <ThemedTextField
                        InputLabelProps={{className: "label-field"}}
                        variant="outlined"
                        value={this.state.commandText}
                        type="text"
                        fullWidth={true}
                        onChange={this.updateCommandText}
                        placeholder={`${this.state.botCommandToken}command ...`}
                        label={(!this.cmdIsValid() && this.state.commandText.length > 0) ? `Invalid command! Format: ${this.state.botCommandToken}command ...` : `Send commands to ${this.state.botName} from here...`}
                        error={this.state.commandText.charAt(0) !== this.state.botCommandToken && this.state.commandText.length > 0}
                        InputProps={
                            {endAdornment: 
                                this.props.type === 'compact' ? 
                                <ThemedIconButton component="span"
                                type="submit"
                                disabled={!this.cmdIsValid}
                                onClick={this.sendCommand}>
                                    <KeyboardReturnIcon/>
                                </ThemedIconButton> :
                                <ThemedSubmitButton component="span"
                                    endIcon={<KeyboardReturnIcon />}
                                    type="submit" 
                                    variant="contained" 
                                    style={{width: '150px', height:'40px'}}
                                    disabled={!this.cmdIsValid}
                                    onClick={this.sendCommand}>
                                    Send
                                </ThemedSubmitButton>
                            }
                        }
                    />                        
                </form>
                </ThemedPaper>
            </Grid>
        );
    }
}

export default CommandFormGrid;