import React from 'react';
import {AppBar, Tabs, Tab, Box, Typography, Toolbar, Grid} from '@material-ui/core';
import styled from 'styled-components';


const ThemedTabs = styled(Tabs)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;

const ThemedToolbar = styled(Toolbar)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
    display: flex;
    justify-content: space-between;
    *:focus {
        outline: 0 !important;
    }
`;

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;
    return (
        <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
        >
        {value === index && (
            <Box p={3}>
            <Typography component={'span'}>{children}</Typography>
            </Box>
        )}
        </div>
    );
}

const tabProps = (index) => {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

class TopNavBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 0,
            title: "JJMumbleBot Dashboard"
        };
    }

    componentDidMount() {
        this.getBotName();
    }

    getBotName = () => {
        fetch('/api/general/name', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => this.setState({title: `${json_data.data.name} Dashboard`}))
        .catch(e => {
            console.trace(e);
            this.setState({title: 'JJMumbleBot Dashboard'});
        });
    };

    handleChange(evt, newVal) {
        this.setState({value: newVal});
    }
    //<img src={logo} className="App-logo" alt="logo" />
    render() {
        return (
            <div className="top-nav-bar" key="top-nav-bar-key">
                <AppBar position="static" key="app-bar-key">
                    <ThemedToolbar key="tool-bar-key">
                        <Typography key="app-bar-text-key" variant="h6">{this.state.title}</Typography>
                        <ThemedTabs key="app-bar-tabs-key" value={this.state.value} onChange={this.handleChange.bind(this)}>
                            <Tab key="tab-commands" label="Commands" {...tabProps(0)} />
                            <Tab key="tab-plugins" label="Plugins" {...tabProps(1)} />
                            <Tab key="tab-debug" label="Debug" {...tabProps(2)} />
                        </ThemedTabs>
                        {this.props.themeToggle} 
                    </ThemedToolbar>
                </AppBar>
                {
                    this.props.tabs.map((item, index) => 
                    <TabPanel value={this.state.value} index={index} key={`${index}-panel`}>
                        <Grid container spacing={3}
                            direction="row"
                            alignItems="flex-start" key={`${index}-grid`}>
                        {item.map(subitem => subitem)}
                        </Grid>
                    </TabPanel>
                    )
                }
            </div>
        );
    }
}

export default TopNavBar;